.Login{
    display: flex;
    height: 100vh;
    background-color: var(--right-color);
    .leftCont{
        flex: 847;
        background: url('../images/logBg.jpg') center center no-repeat;
        background-size: cover;
    }
    .ritCont{
        flex: 520;
        display: flex;
        justify-content: center;
        align-items: center;
        .loginCont{
            width: 300px;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            img{
                margin-bottom: 10px;
            }
            .t-input{
                margin: 12px 0;
                height: 50px;
                border-radius: 8px;
            }
            .t-button{
                width: 100%;
                height: 60px;
                margin-top: 64px;
                border-radius: 60px;
                font-size: 18px;
                a{
                    color: #fff;
                    text-decoration: none;
                    letter-spacing: 8px;
                }
            }
        }
    }
}