.systemSet{
    .title{
        font-size: 18px;
        margin-bottom: 20px;
        color:var(--font-main-color);
        display:flex;
        justify-content: space-between;
        position: relative;
        .off,.on{
            font-size: 14px;
            em{
                width: 8px;
                height: 8px;
                display: inline-block;
                border-radius: 100%;
                margin-right: 5px;
            }
        } 
        .off{
            em{
                background-color: var(--bg-roll-color);
            }
        }
        .on{
            em{
                background: var(--bg-main-color);
            }
        }
    }    
    .mapList{
        margin-bottom: 40px;
    }
}