$SUCCESS_COLOR: #3bd396;
$HOVER_COLOR: #94B8FF;

$MAN_COLOR: #17233E;
$BORDER_COLOR: #DBDDE3;
$BG_COLOR: #F3F4F7;

:root,
:root[theme-mode="light"] {
  --max-width: 1440px;
  --min-width: 1080px; 
  --min-height: calc(100vh - 170px);
  --width: 80%;

  --min-color: #6699ff;
  --hover-color: #94B8FF;
  --active-color: #386CD5;
  --white-color: #ffffff;
  --black-color: #000000;
  --table-back-color: #fafafa;
  --background-color: #F3F4F7;
  --font-main-color:#17233E;
  --font-minor-color:#8992A7;
  --bg-main-color:#D8DDE3;
  --bg-roll-color: #3BD396;
  --bg-error-color: #FF5C5C;
  --font-des-color:BAC0CD;

  --td-brand-color: var(--min-color); 
  --td-brand-color-active: var(--min-color);
  // --td-error-color: $ERROR_ROLOR;
  // --td-success-color: $SUCCESS_COLOR;

   // 基础颜色的扩展 用于 hover / 聚焦 / 禁用 / 点击 等状态
  --td-brand-color-hover: var(--hover-color);     // hover态
  --td-brand-color-focus: var(--hover-color);     // focus态，包括鼠标和键盘
  // --td-brand-color-active: $MIN_COLOR;    // 点击态
  // --td-brand-color-disabled: $MIN_COLOR;  // 禁用态
  // --td-brand-color-light: $MIN_COLOR; 
}

thead tr{
  background-color: var(--table-back-color) !important;
}