.searchCont{
    display: flex;
    .searchitem{
        display: flex;
        align-items: center;
        margin-right: 40px;
        margin-bottom: 30px;
        position: relative;
        .label{
            min-width: 70px;
            padding-right: 10px;
            color: var(--font-minor-color);
            text-align: right;
        }
        .searchIcon{
          position: absolute;
          display: inline-block;
          width: 16px;
          height: 20px;
          font-size: 14px;
          right: 10px;
          color: var(--font-minor-color);
          cursor: pointer;
        }
    }
}