@use 'theme'; // 主题样式 全局变量定义
@use 'login'; 
@use 'systemSet';
@use 'server';
@use 'trajectory';
@use 'fence';
@use 'search_comp';
@use 'trajectory_details';

// body 样式定义
body {
    margin: 0px;
    padding: 0;
    background-color: var(--background-color);
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: var(--font-main-color);
  } 
.link{
  color: var(--min-color);
  cursor: pointer;
}
// header组件样式   
header{
  display: flex;
  align-items: center;
  height: 70px;
  background-color: var(--white-color);
  .logo{
    margin:0 84px 0 25px;
    cursor: pointer;
  }
  nav{
    a, .topTit {
      color: var(--font-main-color);
      font-weight: 600;
      text-decoration: none;
      display: inline-block;
      padding: 8.5px 20px;
      border-radius: 4px;
      cursor: pointer;
    }
    .active{
      color: var(--white-color);
      background: var(--min-color);
    }
  }


  .headerUser{
    display: flex;
    align-items: center;
    position: absolute;
    right: 20px;
    div{
      margin: 0 6px;
      line-height: 20px;
      display: flex;
      align-items: center;
    }
    .line{
      width: 1px;
      box-sizing: content-box;
      margin: 0 20px;
      height: 18.5px;
      background-color: #b6bbc2;
    }
    .back{
      cursor: pointer;
    }
  }
}

// 搜索框的外框结构
.searchCont{
  display: flex;
  justify-content: space-between;
}

// 正状态：成功
.succ{
  color: var(--bg-roll-color)
}
// 负状态：失败
.fail{
  color: var(--bg-error-color)
}
// 面包屑 - 全局
.BreadcrumbCont{
  width: var(--width);
  max-width: var(--max-width);
  min-width: var(--min-width);
  margin: 0 auto;
  margin-top: 40px;
  margin-bottom: -20px;
}
// 全局主体样式
.container{
  width: var(--width);
  max-width: var(--max-width);
  min-width: var(--min-width);
  min-height: var(--min-height);
  background-color: var(--white-color);
  margin: 0 auto;
  margin-top: 40px;
  padding: 40px;
}
// 全局底部样式
footer{
  color: var(--font-minor-color);
  line-height: 60px;
  text-align: center;
}  


//没有数据 
.noData{
   margin: 0 auto;
   height: 60vh;
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
   color: #BAC0CD;
}